




















import Api from '../../api/api.js'
import split from '../../components/split.vue'
import Fetch from '../../util/fetch.js'
let graceLazyload = require('../../util/graceLazyload.js')
export default {
	components:{
		split
	},
	data() {
		return {
			goods: [],
			isShow:[],
			page: 1,
			totalPage: 0
		};
	},
	onPageScroll:function(e){
		graceLazyload.load(e.scrollTop, this);
	},
	mounted(){
		this.getData();
	},
	onShow() {
		uni.pageScrollTo({
			scrollTop:0,
			duration:0
		})
	},
	computed:{
		preClass(){
			if(this.page > 1 && this.totalPage > 1){
				return ''
			} else {
				return 'disabled'
			}
		},
		nextClass(){
			if(this.page < this.totalPage){
				return ''
			} else {
				return 'disabled'
			}
		}
	},
	methods:{
		getData (page) {
			var that = this;
			Fetch.fetchData(Api.TopicList,{
				page: page
			},(res)=>{
				const data = res.data.data;
				that.page = data.currentPage;
				that.goods = data.data;
				that.totalPage = data.totalPages;
				uni.pageScrollTo({
					scrollTop: 0,
					duration:0
				});
				graceLazyload.load(0, that);
			},true)
		},
		getNextData () {
			if(this.page < this.totalPage){
				this.getData(this.page+1);
			}
		},
		getPreData () {
			if(this.page > 1 && this.totalPage > 1){
				this.getData(this.page-1);
			}
		}
	}
}
